}
.ared {
  color: #b12727;
}
.agreen {
  color: #1bcc22;
}
.company-edit-form #result > div,
.region-form #result > div,
.company-form #result > div {
  display: inline-block;
  width: 50%;
}
.company-edit-form #result .at-wrapper,
.region-form #result .at-wrapper,
.company-form #result .at-wrapper {
  display: inline-block;
  width: calc(100% - 170px);
  margin-left: 10px;
  margin-top: 20px;
  vertical-align: top;
}
.company-edit-form #result .result-alt,
.region-form #result .result-alt,
.company-form #result .result-alt {
  margin-bottom: 12px;
}
.form {
  font-family: FiraSans, sans-serif;
  font-weight: 600;
  margin-bottom: 40px;
}
.form .fg-streight {
  padding: 0;
}
.form .old-photo img {
  max-width: 100%;
  height: auto;
}
.form .fg-streight.left {
  padding-right: 5px;
}
.form .fg-streight.right {
  padding-left: 5px;
}
.form .thumbnail-wrapper {
  display: inline-block;
}
.form .thumbnail-wrapper .thumbnail {
  width: 130px;
  height: 100px;
  margin: 10px;
}
.form .admin-tab li {
  width: 33.334%;
  text-align: center;
}
.form .admin-tab li a {
  display: block;
}
.form .tabcontent {
  margin-bottom: 10px;
}
.form input:required {
  border: 1px solid red;
  /* ������� ����� ��� ������������ ����� */
}
.form .required:after {
  content: '*';
  color: #2c8dee;
  padding-left: 1px;
  font-family: FiraSans, sans-serif;
  font-weight: 900;
}
.form .img-label {
  display: block;
}
.form .img-outer-wrapper {
  width: 49%;
  display: inline-block;
  position: relative;
  margin-top: 5px;
}
.form .img-outer-wrapper .at-wrapper {
  display: inline-block;
  width: calc(100% - 190px);
  margin: 0;
  vertical-align: bottom;
}
.form .img-outer-wrapper .drop-image {
  position: absolute;
  right: 0;
  top: 46%;
}
.form .img-outer-wrapper .drop-image .btn {
  border: none;
  background: url(images/icn_trash.png) no-repeat center left;
  box-shadow: none;
  display: inline-block;
  width: 15px;
  height: 15px;
  font-size: 0;
}
.form .published-wrapper {
  min-height: 135px;
}
.form .published-wrapper .form-control {
  box-shadow: none;
  border: none;
}
.form .published-inline .form-control {
  box-shadow: none;
  display: inline-block;
  width: 110px;
  vertical-align: bottom;
}
.form .phones-wrapper .form-control {
  margin-bottom: 5px;
}
.form .chosen-wrapper {
  margin-bottom: 5px;
}
.form .chosen-container .chosen-choices,
.form .chosen-container .chosen-single {
  margin-bottom: 0;
  border-radius: 0;
  color: #333333;
  font-family: FiraSans, sans-serif;
  font-weight: 600;
  width: 100%;
  height: 34px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
}
.form .chosen-container .chosen-choices div b,
.form .chosen-container .chosen-single div b {
  background: url(images/select_arrow.png) no-repeat 0 13px;
}
.form .chosen-container .chosen-choices li.search-field input[type=text],
.form .chosen-container .chosen-single li.search-field input[type=text] {
  height: auto;
}
.form .chosen-container .chosen-choices li.search-choice,
.form .chosen-container .chosen-single li.search-choice {
  margin: 0 5px 3px 0;
}
.form .form-group .btn-success {
  margin-right: 15px;
}
.alttitle-edit-wrapper {
  padding: 0;
}
.alert.success {
  position: absolute;
  z-index: 1;
  margin-top: 15px;
  display: none;
  width: 100%;
  padding: 15px;
  margin-bottom: 20px;
  border: 1px solid #d6e9c6;
  border-radius: 4px;
  color: #3c763d;
  background-color: #dff0d8;
}
.alert {
  padding: 15px;
  margin-bottom: 20px;
  border: 1px solid #bce8f1;
  border-radius: 4px;
  color: #31708f;
  background-color: #d9edf7;
}
.at-delete-button {
  display: inline-block;
}
.apage-title {
  font-size: 20px;
  font-family: FiraSans, sans-serif;
  color: #333333;
  text-align: center;
  margin-top: 30px;
  margin-bottom: 25px;
  font-weight: 700;
}
label {
  font-size: 14px;
  font-family: FiraSans, sans-serif;
  color: #999999;
  font-weight: 600;
}
select,
select.form-control,
textarea,
textarea.form-control,
input,
input.form-control {
  border-radius: 0;
  border-color: #cccccc;
  color: #333333;
  font-family: FiraSans, sans-serif;
  font-weight: 600;
}
select:focus,
select.form-control:focus,
input:focus,
input.form-control:focus,
textarea:focus,
textarea.form-control:focus {
  border-color: #2c8dee;
  box-shadow: none;
}
.form-group .btn {
  font-family: FiraSans, sans-serif;
  font-weight: 600;
  color: black;
  text-align: center;
  border-radius: 0;
  background-image: none;
  border: none;
}
.form-group .btn-success {
  padding: 10px 35px;
  background-color: #fbc032 !important;
  margin-right: 15px;
  outline: none !important;
}
.relative-apage-title {
  position: relative;
}
.logs .msg-errors li {
  display: block;
  width: 95%;
  margin-top: 10px;
  border-radius: 5px;
  border: 1px solid #D20009;
  padding: 10px 0;
  text-indent: 25px;
  font-size: 14px;
  background-color: #f3d9d9;
}
.ui-sortable .top-level .draggable-alias {
  padding-left: 19px;
}
.ui-sortable .top-level .graggable-pub {
  padding-left: 13px;
}
/* Essentials */
html,
div,
map,
dt,
isindex,
form,
header,
aside,
section,
section,
article,
footer {
  display: block;
}
html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue", Helvetica, Arial, Verdana, sans-serif;
  background: #F8F8F8;
  font-size: 12px;
}
.clear {
  clear: both;
}
.spacer {
  height: 20px;
}
a:link,
a:visited {
  color: #77BACE;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
/* Header */
header#header {
  height: 55px;
  width: 100%;
  background: #222222 url(images/header_bg.png) repeat-x;
}
header#header h1.site_title,
header#header h2.section_title {
  float: left;
  margin: 0;
  font-size: 22px;
  display: block;
  width: 23%;
  height: 55px;
  font-weight: normal;
  text-align: left;
  text-indent: 1.8%;
  line-height: 55px;
  color: #fff;
  text-shadow: 0 -1px 0 #000;
}
header#header h1.site_title a {
  color: #fff;
  text-decoration: none;
}
header#header h2.section_title {
  text-align: center;
  text-indent: 4.5%;
  width: 68%;
  background: url(images/header_shadow.png) no-repeat left top;
}
.btn_view_site {
  float: left;
  width: 9%;
}
.btn_view_site a {
  display: block;
  margin-top: 12px;
  width: 91px;
  height: 27px;
  background: url(images/btn_view_site.png) no-repeat;
  text-align: center;
  line-height: 29px;
  color: #fff;
  text-decoration: none;
  text-shadow: 0 -1px 0 #000;
}
.btn_view_site a:hover {
  background-position: 0 -27px;
}
/* Secondary Header Bar */
section#secondary_bar {
  height: 38px;
  width: 100%;
  background: #F1F1F4 url(images/secondary_bar.png) repeat-x;
}
section#secondary_bar .user {
  float: left;
  width: 23%;
  height: 38px;
}
.user p {
  margin: 0;
  padding: 0;
  color: #666666;
  font-weight: bold;
  display: block;
  float: left;
  width: 85%;
  height: 35px;
  line-height: 35px;
  text-indent: 25px;
  text-shadow: 0 1px 0 #fff;
  background: url(images/icn_user.png) no-repeat center left;
  margin-left: 6%;
}
.user a {
  text-decoration: none;
  color: #666666;
}
.user a:hover {
  color: #77BACE;
}
.user a.logout_user {
  float: left;
  display: block;
  width: 16px;
  height: 35px;
  text-indent: -5000px;
  background: url(images/icn_logout.png) center no-repeat;
}
/* Breadcrumbs */
section#secondary_bar .breadcrumbs_container {
  float: left;
  width: 77%;
  background: url(images/secondary_bar_shadow.png) no-repeat left top;
  height: 38px;
}
article.breadcrumbs {
  float: left;
  padding: 0 10px;
  border: 1px solid #ccc;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  -webkit-box-shadow: 0 1px 0 #fff;
  -moz-box-shadow: 0 1px 0 #fff;
  box-shadow: 0 1px 0 #fff;
  height: 23px;
  margin: 4px 3%;
}
.breadcrumbs a {
  display: inline-block;
  float: left;
  height: 24px;
  line-height: 23px;
}
.breadcrumbs a.current,
.breadcrumbs a.current:hover {
  color: #9E9E9E;
  font-weight: bold;
  text-shadow: 0 1px 0 #fff;
  text-decoration: none;
}
.breadcrumbs a:link,
.breadcrumbs a:visited {
  color: #44474F;
  text-decoration: none;
  text-shadow: 0 1px 0 #fff;
  font-weight: bold;
}
.breadcrumbs a:hover {
  color: #222222;
}
.breadcrumb_divider {
  display: inline-block;
  width: 12px;
  height: 24px;
  background: url(images/breadcrumb_divider.png) no-repeat;
  float: left;
  margin: 0 5px;
}
/* Sidebar */
aside#sidebar {
  background: #E0E0E3 url(images/sidebar.png) repeat;
  float: left;
  min-height: 500px;
  margin-top: -4px;
  padding: 5px;
}
#sidebar hr {
  border: none;
  outline: none;
  background: url(images/sidebar_divider.png) repeat-x;
  display: block;
  width: 100%;
  height: 2px;
}
/* Search */
.quick_search {
  text-align: center;
  padding: 14px 0 10px 0;
}
.quick_search input[type=text] {
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border-radius: 20px;
  border: 1px solid #bbb;
  height: 26px;
  width: 90%;
  color: #ccc;
  -webkit-box-shadow: inset 0 2px 2px #ccc, 0 1px 0 #fff;
  -moz-box-shadow: inset 0 2px 2px #ccc, 0 1px 0 #fff;
  box-shadow: inset 0 2px 2px #ccc, 0 1px 0 #fff;
  text-indent: 30px;
  background: #fff url(images/icn_search.png) no-repeat;
  background-position: 10px 6px;
}
.quick_search input[type=text]:focus {
  outline: none;
  color: #666666;
  border: 1px solid #77BACE;
  -webkit-box-shadow: inset 0 2px 2px #ccc, 0 0 10px #ADDCE6;
  -moz-box-shadow: inset 0 2px 2px #ccc, 0 0 10px #ADDCE6;
  box-shadow: inset 0 2px 2px #ccc, 0 0 10px #ADDCE6;
}
/* Sidebar Menu */
#sidebar h3 {
  color: #1F1F20;
  text-transform: uppercase;
  text-shadow: 0 1px 0 #fff;
  font-size: 13px;
  margin: 10px 0 10px 6%;
  display: block;
  float: left;
  width: 90%;
}
.toggleLink {
  color: #999999;
  font-size: 10px;
  text-decoration: none;
  display: block;
  float: right;
  margin-right: 2%;
}
#sidebar .toggleLink:hover {
  color: #77BACE;
  text-decoration: none;
}
#sidebar ul {
  clear: both;
  margin: 0;
  padding: 0;
}
#sidebar li {
  list-style: none;
  margin: 0 0 0 12%;
  padding: 0;
}
#sidebar li a {
  color: #666666;
  padding-left: 25px;
  text-decoration: none;
  display: inline-block;
  height: 17px;
  line-height: 17px;
  text-shadow: 0 1px 0 #fff;
  margin: 2px 0;
}
#sidebar li a:hover {
  color: #444444;
}
/* Sidebar Icons */
#sidebar li.icn_new_article a {
  background: url(images/icn_new_article.png) no-repeat center left;
}
#sidebar li.icn_edit_article a {
  background: url(images/icn_edit_article.png) no-repeat center left;
}
#sidebar li.icn_categories a {
  background: url(images/icn_categories.png) no-repeat center left;
}
#sidebar li.icn_tags a {
  background: url(images/icn_tags.png) no-repeat center left;
}
#sidebar li.icn_add_user a {
  background: url(images/icn_add_user.png) no-repeat center left;
}
#sidebar li.icn_view_users a {
  background: url(images/icn_view_users.png) no-repeat center left;
}
#sidebar li.icn_profile a {
  background: url(images/icn_profile.png) no-repeat center left;
}
#sidebar li.icn_folder a {
  background: url(images/icn_folder.png) no-repeat center left;
}
#sidebar li.icn_photo a {
  background: url(images/icn_photo.png) no-repeat center left;
}
#sidebar li.icn_audio a {
  background: url(images/icn_audio.png) no-repeat center left;
}
#sidebar li.icn_video a {
  background: url(images/icn_video.png) no-repeat center left;
}
#sidebar li.icn_settings a {
  background: url(images/icn_settings.png) no-repeat center left;
}
#sidebar li.icn_security a {
  background: url(images/icn_security.png) no-repeat center left;
}
#sidebar li.icn_jump_back a {
  background: url(images/icn_jump_back.png) no-repeat center left;
}
#sidebar p {
  color: #666666;
  padding-left: 6%;
  text-shadow: 0 1px 0 #fff;
  margin: 10px 0 0 0;
}
#sidebar a {
  color: #666666;
  text-decoration: none;
}
#sidebar a:hover {
  text-decoration: underline;
}
#sidebar footer {
  margin-top: 20%;
}
/* Main Content */
section#main {
  width: 77%;
  min-height: 500px;
  background: url(images/sidebar_shadow.png) repeat-y left top;
  float: left;
  margin-top: -2px;
}
#main h3 {
  color: #1F1F20;
  text-transform: uppercase;
  text-shadow: 0 1px 0 #fff;
  font-size: 13px;
  margin: 8px 20px;
}
/* Modules */
.module {
  border: 1px solid #9BA0AF;
  width: 100%;
  margin: 20px 3% 0 3%;
  margin-top: 20px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  background: #ffffff;
}
#main .module header h3 {
  display: block;
  width: 90%;
  float: left;
}
.module header {
  height: 38px;
  width: 100%;
  background: #F1F1F4 url(images/secondary_bar.png) repeat-x;
  -webkit-border-top-left-radius: 5px;
  -webkit-border-top-right-radius: 5px;
  -moz-border-radius-topleft: 5px;
  -moz-border-radius-topright: 5px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.module footer {
  height: 32px;
  width: 100%;
  border-top: 1px solid #9CA1B0;
  background: #F1F1F4 url(images/module_footer_bg.png) repeat-x;
  -moz-border-radius-bottomleft: 5px;
  -moz-border-radius-bottomright: 5px;
  -webkit-border-bottom-left-radius: 5px;
  -webkit-border-bottom-right-radius: 5px;
}
.module_content {
  margin: 10px 20px;
  color: #666;
}
/* Module Widths */
.width_full {
  width: 95%;
}
.width_half {
  width: 46%;
  margin-right: 0;
  float: left;
}
.width_quarter {
  width: 26%;
  margin-right: 0;
  float: left;
}
.width_3_quarter {
  width: 66%;
  margin-right: 0;
  float: left;
}
/* Stats Module */
.stats_graph {
  width: 64%;
  float: left;
}
.stats_overview {
  background: #F6F6F6;
  border: 1px solid #ccc;
  float: right;
  width: 26%;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
.overview_today,
.overview_previous {
  width: 50%;
  float: left;
}
.stats_overview p {
  margin: 0;
  padding: 0;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 0 1px 0 #fff;
}
.stats_overview p.overview_day {
  font-size: 12px;
  font-weight: bold;
  margin: 6px 0;
}
.stats_overview p.overview_count {
  font-size: 26px;
  font-weight: bold;
  color: #333333;
}
.stats_overview p.overview_type {
  font-size: 10px;
  color: #999999;
  margin-bottom: 8px;
}
/* Content Manager */
.tablesorter {
  width: 100%;
  margin: -5px 0 0 0;
}
.tablesorter td {
  margin: 0;
  padding: 0;
  border-bottom: 1px dotted #ccc;
}
.tablesorter thead tr {
  height: 34px;
  background: url(images/table_sorter_header.png) repeat-x;
  text-align: left;
  text-indent: 10px;
  cursor: pointer;
}
.tablesorter td {
  padding: 3px 10px;
}
.tablesorter input[type=image] {
  margin-right: 10px;
}
ul.tabs {
  margin: 3px 10px 0 0;
  padding: 0;
  float: right;
  list-style: none;
  height: 24px;
  /*--Set height of tabs--*/
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  -webkit-box-shadow: 0 1px 0 #fff;
  -moz-box-shadow: 0 1px 0 #fff;
  box-shadow: 0 1px 0 #fff;
  border: 1px solid #ccc;
  font-weight: bold;
  text-shadow: 0 1px 0 #fff;
}
ul.tabs li {
  float: left;
  margin: 0;
  padding: 0;
  line-height: 24px;
}
ul.tabs li a {
  text-decoration: none;
  color: #999;
  display: block;
  padding: 0 10px;
  height: 24px;
}
ul.tabs li a:hover {
  color: #44474F;
}
html ul.tabs li.active a {
  color: #44474F;
}
html ul.tabs li.active,
html ul.tabs li.active a:hover {
  background: #F1F2F4;
  -webkit-box-shadow: inset 0 2px 3px #818181;
  -moz-box-shadow: inset 0 2px 3px #818181;
  box-shadow: inset 0 2px 3px #818181;
}
html ul.tabs li:first-child,
html ul.tabs li:first-child a {
  -webkit-border-top-left-radius: 5px;
  -webkit-border-bottom-left-radius: 5px;
  -moz-border-radius-topleft: 5px;
  -moz-border-radius-bottomleft: 5px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
html ul.tabs li:last-child,
html ul.tabs li:last-child a {
  -webkit-border-top-right-radius: 5px;
  -webkit-border-bottom-right-radius: 5px;
  -moz-border-radius-topright: 5px;
  -moz-border-radius-bottomright: 5px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
#main .module header h3.tabs_involved {
  display: block;
  width: 60%;
  float: left;
}
/* Messages */
.message {
  border-bottom: 1px dotted #cccccc;
}
.post_message {
  text-align: left;
  padding: 5px 0;
}
.post_message input[type=text] {
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  border: 1px solid #bbb;
  height: 20px;
  width: 70%;
  color: #ccc;
  -webkit-box-shadow: inset 0 2px 2px #ccc, 0 1px 0 #fff;
  -moz-box-shadow: inset 0 2px 2px #ccc, 0 1px 0 #fff;
  box-shadow: inset 0 2px 2px #ccc, 0 1px 0 #fff;
  text-indent: 10px;
  background-position: 10px 6px;
  float: left;
  margin: 0 3.5%;
}
.post_message input[type=text]:focus {
  outline: none;
  border: 1px solid #77BACE;
  -webkit-box-shadow: inset 0 2px 2px #ccc, 0 0 10px #ADDCE6;
  -moz-box-shadow: inset 0 2px 2px #ccc, 0 0 10px #ADDCE6;
  box-shadow: inset 0 2px 2px #ccc, 0 0 10px #ADDCE6;
  color: #666666;
}
.post_message input[type=image] {
  float: left;
}
.message_list {
