/* New/Edit Article Module */
fieldset {
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  background: #F6F6F6;
  border: 1px solid #ccc;
  padding: 1% 0%;
  margin: 10px 0;
}
fieldset label {
  display: block;
  float: left;
  width: 200px;
  height: 25px;
  line-height: 25px;
  text-shadow: 0 1px 0 #fff;
  font-weight: bold;
  padding-left: 10px;
  margin: -5px 0 5px 0;
  text-transform: uppercase;
}
fieldset input[type=text] {
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  border: 1px solid #BBBBBB;
  height: 20px;
  color: #666666;
  -webkit-box-shadow: inset 0 2px 2px #ccc, 0 1px 0 #fff;
  -moz-box-shadow: inset 0 2px 2px #ccc, 0 1px 0 #fff;
  box-shadow: inset 0 2px 2px #ccc, 0 1px 0 #fff;
  padding-left: 10px;
  background-position: 10px 6px;
  margin: 0;
  display: block;
  float: left;
  width: 96%;
  margin: 0 10px;
}
fieldset input[type=text]:focus {
  outline: none;
  border: 1px solid #77BACE;
  -webkit-box-shadow: inset 0 2px 2px #ccc, 0 0 10px #ADDCE6;
  -moz-box-shadow: inset 0 2px 2px #ccc, 0 0 10px #ADDCE6;
  box-shadow: inset 0 2px 2px #ccc, 0 0 10px #ADDCE6;
}
fieldset select {
  width: 96%;
  margin: 0 10px;
  border: 1px solid #bbb;
  height: 20px;
  color: #666666;
}
fieldset textarea {
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  border: 1px solid #BBBBBB;
  color: #666666;
  -webkit-box-shadow: inset 0 2px 2px #ccc, 0 1px 0 #fff;
  -moz-box-shadow: inset 0 2px 2px #ccc, 0 1px 0 #fff;
  box-shadow: inset 0 2px 2px #ccc, 0 1px 0 #fff;
  padding-left: 10px;
  background-position: 10px 6px;
  margin: 0 0.5%;
  display: block;
  float: left;
  width: 96%;
  margin: 0 10px;
}
fieldset textarea:focus {
  outline: none;
  border: 1px solid #77BACE;
  -webkit-box-shadow: inset 0 2px 2px #ccc, 0 0 10px #ADDCE6;
  -moz-box-shadow: inset 0 2px 2px #ccc, 0 0 10px #ADDCE6;
  box-shadow: inset 0 2px 2px #ccc, 0 0 10px #ADDCE6;
}
.submit_link {
  float: right;
  margin-right: 3%;
  padding: 5px 0;
}
.submit_link select {
  width: 150px;
  border: 1px solid #bbb;
  height: 20px;
  color: #666666;
}
